<template>
    <el-row :gutter="20">
        <el-col :span="24">
            <div class="space-inner">
                <el-tabs type="border-card" class="lycard" v-model="activeName" @tab-change="handleTabChage">
                    <el-skeleton :rows="rows" :animated="animated" :count="count" :loading="loading"
                                 style="padding: 20px;width: auto;overflow: hidden;">
                        <template #default>
                            <el-tab-pane label="简历学历分布" name="tab1">
                                <ly-line-echart ref="lyecharts1" v-if="activeName == 'tab1'"
                                                :grow-data="growData"></ly-line-echart>
                            </el-tab-pane>
                            <el-tab-pane label="新版学历分布" name="tab2">
                                <gyz-new-pie-echart ref="lyecharts2" v-if="activeName == 'tab2'"></gyz-new-pie-echart>
                            </el-tab-pane>
                            <el-tab-pane label="工作年限统计" name="tab3">
                                <ly-bar-echart ref="lyecharts3" v-if="activeName == 'tab3'"></ly-bar-echart>
                            </el-tab-pane>
                            <el-tab-pane label="雷达图" name="tab4">
                                <gyz-radar-echart ref="lyecharts4" v-if="activeName == 'tab4'"></gyz-radar-echart>
                            </el-tab-pane>
                        </template>
                    </el-skeleton>
                </el-tabs>
            </div>
        </el-col>
    </el-row>
</template>

<script>
    import LyBarEchart from "./barEchart";
    import LyLineEchart from "./pieEchart";
    import GyzNewPieEchart from "./newPieEchart"
    import GyzRadarEchart from "./radarEchart"


    export default {
        name: "LyEchartcard",
        components: {LyLineEchart, LyBarEchart, GyzNewPieEchart, GyzRadarEchart},
        data() {
            return {
                activeName: "tab1",
                dataList: "",
                growData: [

                ],
            }
        },
        created() {
            this.dataList = this.modelValue
            console.log(this.dataList)
            for (const item of this.dataList) {
                this.growData.push({
                    value: item.nums,
                    name: item.title
                })
            }
            console.log("===============growData")
            console.log(this.growData)
            console.log("===============growData")
        },
        props: {
            loading: {
                type: Boolean,
                default: true
            },
            count: {
                type: Number,
                default: 1,
            },
            rows: {
                type: Number,
                default: 4,
            },
            animated: {
                type: Boolean,
                default: true,
            },
            modelValue: {
                type: Array,
                default: []
            },
            chartData: {
                type: Array,
                default: []
            },
            height: {
                type: Number,
                default: 300,
            }
        },
        watch: {
            modelValue: function (nval) {
                this.dataList = nval;
            },
            dataList: function (nval) {
                this.$emit('update:modelValue', nval);
            },
        },
        methods: {
            handleTabChage(e) {
            }
        },
    }
</script>

<style scoped>
    .space-inner {
    }

    .lycard {
        background: var(--el-bg-color);
        box-shadow: var(--el-box-shadow-light);
        border: 1px solid var(--el-border-color-light);
    }
</style>